.memoryLane{
    padding: 0 15rem 0 15rem !important;
    margin-top: 10px;
}

.fxTp{
    position: fixed;
    top: 67px;
    right: 0;
    left: 0;
    z-index: 999;
}

.btnWrpr{
    display: inline-flex;
    margin-left: auto;
    margin-right: auto;
}

.laneBtn{
    padding: 2px 10px 2px 10px;
    margin: 0 5px 0 5px;
    background: #b58dff;
    border-radius: 50px;
    color: #f6f6f6;
    cursor: pointer;
    transition-duration: 0.3s;
    box-shadow:  1px 0px 1px #ac86f2, -1px 0px 2px #be94ff;
}

.laneBtn:hover{
    background: #a676ff;
    color: #ffffff;
}

.laneSlctd{
    padding: 2px 10px 2px 10px;
    margin: 0 5px 0 5px;
    background: #a677ff;
    border-radius: 50px;
    color: #f6f6f6;
    cursor: pointer;
    border: #7b3eec solid 2px;
    transition-duration: 0.3s;
}

.laneSlctd:hover{
    background: #a676ff;
    color: #ffffff;
}

@media screen and (max-width: 766px){
    .memoryLane{
        padding: 0 !important;
    }
}