.selectedImg{
    width: 160px;
    height: 160px;
    object-fit: cover;
}

.imgCov{
    position: relative;
    width: fit-content;
}

.imgCol{
    padding: 0 0 0 0 !important;
}

.removeImg{
    position: absolute;
    right: 6px;
    color: #555555;
    font-weight: 600;
    background: #b6b6b6;
    width: 24px;
    margin-top: 5px;
    cursor: pointer;
    padding: 0 0 0 4px;
}

.removeImg:hover{
    background: #cecece;
    color: #252525;
}

@media screen and (max-width: 880px){
    .selectedImg{
        width: 140px;
        height: 140px;
    }
}

@media screen and (max-width: 766px){
    .selectedImg{
        width: 120px;
        height: 120px;
    }
}

@media screen and (max-width: 576px){
    .selectedImg{
        margin-bottom: 5px;
    }
}

@media screen and (max-width: 600px){
    .selectedImg{
        width: 100px;
        height: 100px;
    }
}